/* Skills Animation */

.skills {
  height: 10vh;
  position: absolute;
  top: 2%;
  display: flex;
  justify-content: center;
  animation: move-rtl 13s linear infinite;
}

.skills h1 {
  font-size: 1.3em;
  font-weight: bold;
  color: #ffffff;
  padding: 0 2rem;
  white-space: nowrap;
  text-transform: uppercase;
}

@keyframes move-rtl {
  0% {
    transform: translateX(110%);
  }

  100% {
    transform: translateX(-120%);
  }
}
  

.left {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    color: rgb(255, 255, 255);
}

.left-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: inherit;
    padding-right: 2%;
}

.greet {
    font-size: 2.5em;
    font-weight: 300;
}

.handWave {
  margin-left: 0.3em;
  margin-bottom: 0.3em;
  width: 45px;
}

.name {
  font-size: 3em;
}

.getToKnow {
  left: 10%;
  bottom: 15%;
  position: absolute;
  background-color: transparent;
  /* -webkit-animation: vibrate-1 0.3s linear infinite both;
  animation: vibrate-1 0.3s linear infinite both; */
  cursor: pointer;
  color: #ffffff;
  font-size: 1.5rem;
  font-weight: 700;
  border: 4px solid #ffffff;
  padding: 0.5% 1%;
  border-radius: 0.25em;
  width: 250px;
  white-space: nowrap;
  text-shadow: 0 0 0.125em rgb(15, 24, 101), 0 0 0.45em #ffffff;
  box-shadow: inset 0 0 0.5em #ffffff, 0 0 0.5em 0 #ffffff;
}

.getToKnow::before {
  pointer-events: none;
  content: '';
  position: absolute;
  background-color: rgb(255, 255, 255);
  top: 120%;
  left: 0;
  width: 100%;
  height: 100%;
  transform: perspective(2em) rotateX(40deg) scale(1, 0.35);
  filter: blur(2em);
}

.getToKnow::after {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(255, 255, 255, 0.251);
  box-shadow: 0 0 1em 0.5em #ffffff;
  opacity: 0;
  transition: opacity 100ms linear;
}

.getToKnow:hover, .getToKnow:focus {
  text-shadow: none;
}

.getToKnow:hover::after {
  opacity: 1;
}

.myImg {
  max-height: 80vh;
  min-height: 50vh;
  max-width: 100vw;
  object-fit: cover;
  position: relative;
}

